import { useContext, useState } from "react";
import { useNavigate } from "react-router-dom";
import { Form, Button, message, Layout, Typography, Image, } from "antd";
import { AuthContext } from "../../Store/AuthContext";
import { FiUser, FiLock, FiNavigation } from "react-icons/fi";
import "./auth.css";
import { mainColor } from "../../Utils/colors";
import { Card, FormInput, FormPassword } from "../../Components";
import FooterMenu from "../../Components/Sidebar/FooterMenu";

const { Content } = Layout;
const { Title, Text } = Typography;

const SignIn = () => {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [loading, setLoading] = useState(false);

  const { signIn } = useContext(AuthContext);
  const navigate = useNavigate();

  const handleSignIn = async () => {
    setLoading(true);
    if (!email || !password) {
      message.warning("Form tidak boleh kosong");
      setLoading(false);

      return null;
    }

    if (password.length < 8) {
      message.warn("Password panjang minimal 8 karakter");
      setLoading(false);

      return null;
    }

    try {
      const login = await signIn(email, password);

      message.success(`Hello.. Welcome ${login.user?.email}`);
      navigate("/");

      return Promise.resolve(login);
    } catch (error) {
      if (error.response) {
        message.error(
          `${error.response.data.error.status_code}, ${error.response.data.error.message}`
        );
        console.log("error response: ", error.response);
        setLoading(false);

        return null;
      }

      message.error("Something went wrong, please try again..");
      console.log("error signin: ", error);
      setLoading(false);
    }
  };

  const onFinish = (values) => {
    console.log("Success");
  };

  const onFinishFailed = (errorInfo) => {
    console.log("Failed:", errorInfo);
  };
  return (
    <Layout style={{ minHeight: "100vh" }}>
      <Content>
        <div className="auth-header">
          <Card
            style={{
              marginLeft: 10,
              marginRight: 10,
              marginTop: 20,
              minWidth: 320,
            }}
          >
            <div className="auth-header-content">
              <Image src="/bmg_logo.png" width={100} alt="logobmg" />
              <Title style={{ color: mainColor, fontWeight: "bold", marginTop: 20, marginBottom: 0 }}>
                Welcome,
              </Title>
              <Text type="secondary">Sign in to continue!</Text>
            </div>
            <Form
              name="basic"
              onFinish={onFinish}
              onFinishFailed={onFinishFailed}
              autoComplete="off"
            >
              <FormInput
                name="email"
                required={true}
                message="Please input your email!"
                prefix={<FiUser color="rgba(0,0,0,.25)" />}
                placeholder="enter your email"
                value={email}
                onChange={(val) => setEmail(val.target.value)}
              />
              <FormPassword
                name="password"
                required={true}
                message="Please input your password!"
                prefix={<FiLock color="rgba(0,0,0,.25)" />}
                placeholder="enter your password"
                value={password}
                onChange={(val) => setPassword(val.target.value)}
              />
              <Form.Item>
                <Button
                  type="primary"
                  shape="round"
                  icon={<FiNavigation style={{ marginRight: "6px" }} />}
                  size="large"
                  onClick={handleSignIn}
                  loading={loading}
                  style={{ width: "100%" }}
                  htmlType="submit"
                >
                  Sign In
                </Button>
              </Form.Item>
            </Form>
          </Card>
        </div>
      </Content>
      <FooterMenu />
    </Layout>
  );
};

export default SignIn;
